.footer {
    background: var(--absolute);
    color: var(--text-primary);
    height: 50px;
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: center;

    @media (max-width: 2560px) {
        max-width: 2560px;
        padding: 0 500px;
    }
    @media (max-width: 1920px) {
        padding: 0 200px;
    }
    @media (max-width: 1240px) {  
        padding: 0 100px;
    }
    @media (max-width: 800px) {
        padding: 0 25px;
    }
    @media (max-width: 650px) {
        padding: 0 25px;
        &-menu {
            display: none;
        }
    }
    @media (max-width: 500px) {
        padding: 0 10px;
    }
}