.header {
    background: #000000;
    height: 50px;
    color: var(--text-tertiary);
    display: flex;
    justify-content: space-between;
    align-items: center;
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    z-index: 999;
    &-menu {
        display: flex;
        &__item {
            margin: 0 25px; 
        }
    }
    &-menu__item, .tel, .socials {
        color: var(--text-primary);
    }
    .tel:hover, .socials:hover {
        color: var(--accent);
    }
    @media (max-width: 2560px) {
        padding: 0 500px;
        .mobile-header {
            display: none;
        }
    }
    @media (max-width: 1920px) {
        padding: 0 200px;
    }
    @media (max-width: 1240px) {  
        padding: 0 100px;
        span {
            display: none;
        }
    }
    @media (max-width: 800px) {
        padding: 0 25px;
    }
    @media (max-width: 650px) {        
        height: 0;
        &-menu, .tel, .socials {
            display: none;
        }
        .mobile-header {
            display: flex;
        }
    }
    @media (max-width: 500px) {
        padding: 0 10px;
    }
}

.mobile {
    &-header {
        background-color: var(--absolute);
        padding: 0 25px;    
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        align-items: center;
        position: fixed;
        top: 0;
        left: 0;
        right: 0;
        height: 50px;
        z-index: 999;
        .brands {
            display: flex;
            flex-direction: row;
            svg {
                margin-right: 15px;
            }
        }
        svg {
            color: var(--text-primary);
        }
    }
    &-menu {        
        background: linear-gradient(
            to bottom, 
            #ffffff 50%,
            #ffffff00 100%);
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        text-align: center;
        overflow: hidden;
        position: fixed;
        top: 0;
        left: 0;
        right: 0;
        z-index: 99;
        transition: .5s;
        &__item {
            font-size: 1.5em;
            padding: 10px 0;
            text-decoration: none;
            color: var(--text-tertiary);
        }
        &-subscribe {
            width: 150px;
            height: 150px;
            margin-top: 100px;
            border-radius: 50%;
            text-align: center;
            vertical-align: middle;
            position: relative;
            &-btn {    
                background: var(--accent);
                color: var(--text-primary);    
                width: 100px;
                height: 100px;
                border-radius: 50%;
                position: absolute;
                top: 50%;
                left: 50%;    
                transform: translate(-50%, -50%);
                z-index: 99;
                i {
                    color: var(--text-primary);
                    position: absolute;
                    top: 50%;
                    left: 50%;    
                    transform: translate(-50%, -50%);
                }
            }
        }
        svg {
            color: var(--text-tertiary);
            position: absolute;
            top: 10px;
            right: 25px;
        }
    }
}

.hidden-menu {
    height: 0;
    opacity: 0;
}

.visible-menu {
    height: 100vh;
    opacity: 1;
}
