:root {
    --absolute: #000000;
    --primary-color: #202124;
    --secondary-color: #373737b2;
    --third-color: #f4f4f4;

    --page-background: #202124;
    --sidebar-background: #373737;
    --content-background: #202124;

    --text-primary: #f4f4f4;
    --text-subhaead: #a0a0a0;
    --text-secondary: #373737;
    --text-tertiary: #202124;

    --accent: #ed037c;
    --destructive: #f74e4e;

    --desktop-width: 1920px;
    --laptop-width: 1240px;
    --tablet-width: 800px;
    --mobile-width: 650px;
    --s-mobile-width: 500px;
}

