*, ::before, ::after { box-sizing: border-box; }

html, body {
    margin: 0;
    padding: 0;
    font-family: 'Comfortaa', sans-serif;
    &::-webkit-scrollbar {
        width: 0;
    }
    scroll-behavior: smooth;
}

body {
    background-repeat: no-repeat;
    background-size: cover;
    background-attachment: fixed;
    height: 100vh;    
    overflow-y: scroll;
    &::-webkit-scrollbar {
        width: 5px;
    } 
    &::-webkit-scrollbar-track {
        background: var(--absolute);
    }    
    &::-webkit-scrollbar {
        background: var(--content-background);
    }    
    &::-webkit-scrollbar-thumb {
        background: var(--accent);
    }    
    @media (max-width: 2560px) {      
        background-image: url('assets/imgs/bg.webp');  
        font-size: 20px;
    } 
    @media (max-width: 1920px) {        
        font-size: 16px;
    }
    @media (max-width: 800px) {      
        font-size: 16px;
    }
    @media (max-width: 500px) {     
        background-image: url('assets/imgs/mobile-bg.webp');    
        font-size: 14px;
    }
}

h1, h2, h3, h4, h5 {
    color: var(--accent);
}

h2 { font-size: 2em; }
h3 { font-size: 1.5em; }

.container {
    margin: 0 auto;
    @media (max-width: 2560px) {
        max-width: 2560px;
        padding: 5vh 500px;
    }
    @media (max-width: 1920px) {
        max-width: 1920px;
        padding: 5vh 200px;
    }
    @media (max-width: 1240px) { 
        max-width: 1240px;      
        padding: 5vh 100px;
    }
    @media (max-width: 800px) {
        max-width: 800px;
        padding: 5vh 25px;
    }
    @media (max-width: 650px) {
        max-width: 650px;
        padding: 5vh 25px;
    }
    @media (max-width: 500px) {
        max-width: 500px;
        padding: 5vh 10px;
    }
}

ul {    
    margin: 0;
    padding: 0;
    list-style: none;
}

a { 
    text-decoration: none;
    transition: .3s ease;
    &:hover {
        color: var(--accent);
    } 
}

.socials, .tel {
    display: flex;
    align-items: center;
    color: var(--text-tertiary);
    svg {
        margin: 0 15px;
    }
}

@keyframes rotate {
	100% {
		transform: rotate(1turn);
	}
}

.btn {
    display: block;
    width: 200px;
    height: 50px;
    border-width: 1px;
    border-style: solid;
    border-radius: 5px;
    text-align: center;
    line-height: 50px;
    transition: .3s; 
    &-pink {
        border-color: var(--accent);
        color: var(--accent);
        &:hover {            
            border-color: var(--absolute);
            color: var(--absolute);
        }
    }
    &-black {
        background: var(--absolute);
        border-color: var(--absolute);
        color: var(--text-primary);
        &:hover {
            background: none;            
            border-color: var(--accent);
        }
    }
}

.subscribe {
    width: 150px;
    height: 150px;
    border-radius: 50%;
    text-align: center;
    vertical-align: middle;
    position: fixed;
    @media (max-width: 1920px) {
        bottom: 75px;
        right: 50px;
    }
    @media (max-width: 800px) {
        bottom: 50px;
        right: 25px;
    }
    @media (max-width: 500px) {
        display: none;
    }
    &-btn {    
        background: var(--accent);
        color: var(--text-primary);    
        width: 100px;
        height: 100px;
        border-radius: 50%;
        position: absolute;
        top: 50%;
        left: 50%;    
        transform: translate(-50%, -50%);
        z-index: 99;
        i {
            color: var(--text-primary);
            position: absolute;
            top: 50%;
            left: 50%;    
            transform: translate(-50%, -50%);
        }
    }
    & a:hover {
        color: var(--text-primary);
    }
}

.ping {
    background: var(--accent);
    width: inherit;
    height: inherit;
    border-radius: 50%;
    animation: ping 1.5s ease-in-out infinite both;
}

@keyframes ping {
    0% {
      transform: scale(0.2);
      opacity: 0.8;
    }
    80% {
      transform: scale(1.2);
      opacity: 0;
    }
    100% {
      transform: scale(2.2);
      opacity: 0;
    }
  }
