.section {
    &-title, &-description {
        text-align: center;
    }
    &-description {
        margin-bottom: 75px;
        @media (max-width: 500px) {
            margin-bottom: 15px;
        }
    }
    &:nth-child(1) {        
        background: linear-gradient(
            to bottom, 
            #ffffff 20%,
            #ffffff00 40%);
    }
    &:nth-child(2) {
        background: linear-gradient(
            to bottom, 
            #ffffff 0%, 
            #ffffff 75%, 
            #ffffffcc 100%);
    }
    &:nth-child(3) {        
        background: #ffffffcc;
    }
    &:nth-child(4) {        
        background: linear-gradient(
            to bottom, 
            #ffffffcc 0%, 
            #ffffff 25%, 
            #ffffff 100%);
    }
    @media (max-width: 800px) {
        background: none;
    }
}
